
import { useColorScheme, useFilter, useHistory, useSearchBoxToggle, useSingleNumber, useToggleStore } from "../../Store/Store";
import { BsClockHistory } from 'react-icons/bs';





const SearchBoxExtension = () => {

    const Color = useColorScheme(state => state.data)
    const setNumber = useSingleNumber(state => state.setData)
    const setToggle = useToggleStore(state => state.setToggle)
    const setFalse = useSearchBoxToggle(state => state.setFalse)
    const setHistory = useHistory(state => state.setData)
    const history = useHistory(state => state.history)
    let his = []

    const handleClick = (item) => {
        setNumber(item);
        setFalse();
        setToggle();

        if(history.length === 0){
            setHistory([{name: item, icon: <BsClockHistory />}])
        }else{
            his =  history.filter(x => {
               return( x.name !== item )
            })
            setHistory([{name: item, icon: <BsClockHistory />}, ...his])
        }

    }

    return(
       <div className="search-extension" style={{background: Color.Primary}} >
          <ul className="suggestion-list" >
              {useFilter(state => state.data).slice(0, 8).map(item => {
                  return(<div key={item.name}>
                  <div id="list-line"></div>
                  <li onClick={() => handleClick(item.name)}><div>{item.icon}</div>{item.name} </li>
                  </div>
                  )
              }) }
          </ul>
       </div>
    )
}

export default SearchBoxExtension;