import create from 'zustand'


const toggleStore = (set) => ({
    toggle: false,
    setToggle: () => set((state) => ({toggle: !state.toggle}))
})

const dark = (set) => ({
    toggle: true,
    setToggle: () => set((state) => ({toggle: !state.toggle})),
    setTrue: () => set(() => ({toggle: true})),
    setFalse: () => set(() => ({toggle: false}))
})

const color = (set) => ({
    data: {},
    setColor: (colors) => set(() => ({data: colors}))
})

const searchBoxToggle = (set) => ({
    toggle: false,
    setFalse: () => set(() => ({toggle: false})),
    setTrue: () => set(() => ({toggle: true}))
})

