import './App.css';
import './Stylesheets/component.css'
import './Stylesheets/body.css'
import './Stylesheets/extension.css'

import React, { useEffect } from 'react';
import Component  from './Components/Component';
import { getAllNumbers, getHalalNumbers, getVeganAvoid, getVeganNumbers, getVegetarianAvoid } from './Connection/getAllNumbers'
import {  useAllNumber, useColorScheme, useHalalNumbers, useMode, useNonVegan, useNonVegetarian, useVeganNumbers } from './Store/Store'
import { Color } from './Colors/Color'
function App() {

   const setData = useAllNumber(state => state.setData)
   const setHalal = useHalalNumbers(state => state.setData)
   const setVegan = useVeganNumbers(state => state.setData)
   const setNonVegan = useNonVegan(state => state.setData)
   const setNonVegetarian = useNonVegetarian(state => state.setData)
   const toggleColor = useMode(state => state.toggle)
   const setColor = useColorScheme(state => state.setColor)
   const contant = "yo"
  
   const x = window.matchMedia('(max-width: 500px')
   const setTrue = useMode(state => state.setTrue)
   const setFalse = useMode(state => state.setFalse)

   if(x.matches){
      setTrue();
   }else{
      setFalse();
   }

   useEffect(() => {
       getAllNumbers(setData);
       getVeganNumbers(setVegan);
       getVeganAvoid(setNonVegan)
       getHalalNumbers(setHalal)
       getVegetarianAvoid(setNonVegetarian)
      },[contant, setData, setHalal, setVegan, setNonVegan, setNonVegetarian])
      
 
     if(toggleColor){
        setColor(Color.light)
     }else{
        setColor(Color.dark)
     }

   return (
       <div className="App">
          <Component />
       </div>  
   );
}

export default App;
