import { useColorScheme, useSearchBoxToggle } from '../Store/Store';
import Body from './Body/Body'
import SearchBox from './Body/SearchBox';
import SearchBoxExtension from './Body/SearchBoxExtension';
import Extension from './Extension/Extension';


const Component = () => {

    const Color = useColorScheme(state => state.data)
    const searchToggle = useSearchBoxToggle(state => state.toggle)
    const setFalse = useSearchBoxToggle(state => state.setFalse)

    return (
       <> 
        <div style={{background: Color.Primary}} className="component-wrap active" onClick={() => setFalse()}>
            <Body/>
            <Extension /> 
            <div className="round-circle" style={{background: Color.Primary}} ></div>
        </div>
        <SearchBox />
        {searchToggle ?  <SearchBoxExtension /> : null }
       </> 
    );
}

export default Component;