import React, { useRef } from 'react';
import { BsSearch } from 'react-icons/bs';
import { IconContext } from "react-icons";
import { useAllNumbersList, useColorScheme, useFilter, useHistory, useSearchBoxToggle, useSingleNumber } from '../../Store/Store';
import { AiOutlineSearch } from 'react-icons/ai';
import { AiOutlineCloseCircle } from 'react-icons/ai';


function SearchBox() {

    const Color = useColorScheme(state => state.data)
    const enums = useAllNumbersList(state => state.data)
    const setFilter = useFilter(state => state.setData)
    const setTrue = useSearchBoxToggle(state => state.setTrue)
    const singleNumber = useSingleNumber(state => state.data)
    const ref = useRef();
    const history = useHistory(state => state.history)

   
    if(singleNumber.length !== 0){ ref.current.value = "" }

    const handleChange = (e) => {
      if(e.target.value !== ''){
        const filteredItems =  enums.filter(num => {
            return(num.toLowerCase().match(e.target.value.toLowerCase()))
        })
        setFilter(filteredItems.map(x => {
            return ({name: x, icon: <AiOutlineSearch />})
        }))
        setTrue();
      }
    }


    const handleSelect = (e) => {
        if(e.target.value === ''){
           setFilter(history)
           setTrue();
        }
    }

    const handleClick = () => {
        ref.current.focus();
    }

    return (
        <div className="search-box" style={{background: Color.Primary}}>
            <div className="active-cross">
               <IconContext.Provider value={{ color: Color.Secondary, size: '1.4em' }}> 
                  <AiOutlineCloseCircle onClick={() => console.log("he")} /> 
               </IconContext.Provider>   
            </div>
            <input id='input-box' ref={ref} onSelect={(e) => handleSelect(e)} onChange={(e) => handleChange(e)} type="text" placeholder="Search" style={{background: Color.Primary, color: Color.Secondary}} />
            <IconContext.Provider value={{ color: Color.Secondary, className: "search-icon", size: '1.4em' }}> 
               <BsSearch onClick={() => handleClick()} />
            </IconContext.Provider>
        </div>
    );
}

export default SearchBox;