
import React, { useEffect } from 'react'
import { useAllNumber, useAllNumbersList, useColorScheme, useHalalNumbers, useIndex, useNonVegan, useSingleNumber, useToggleStore, useVeganNumbers } from "../../Store/Store";




const Extension = () => {

const Color = useColorScheme(state => state.data)  
const toggle = useToggleStore(state => state.toggle)
const setToggle = useToggleStore(state => state.setToggle)
const clearSingleNum = useSingleNumber(state => state.clearData)
const item = useSingleNumber(state => state.data)

// E-numbers data
const allNumbers = useAllNumber(state => state.data).find(x => { return(x.Number.match(item))})
const halalNumbers = useHalalNumbers(state => state.data).find(x => { return(x.Number.match(item))})
const veganNumbers = useVeganNumbers(state => state.data).find(x => { return(x.number.match(item) && x.number.length === item.length )})
const nonVeganNumbers = useNonVegan(state => state.data).find(x => { return(x.Number.match(item) )})
const setSingleNumber = useSingleNumber(state => state.setData)
const numberList = useAllNumbersList(state => state.data)
let num = ""
let halalColor = ""
let safeColor = ""

useAllNumbersList(state => state.data).find((x, index) => { 
 num = index
 return(x.match(item))})


const indexNum = useIndex(state => state.data);
const clearIndex = useIndex(state => state.clearData)
const setIndex = useIndex(state => state.setData)

const handleNextClick = () => {
  clearIndex()
  setIndex(num + 1)
}

useEffect(() => {
 numberList.find((x,index) => {
    setSingleNumber(x)
   return(index === indexNum)
 })
}, [indexNum, numberList, setSingleNumber])



const handleClose = () => {
  clearSingleNum()
  setToggle()
  clearIndex();
}

if(halalNumbers){
  switch(halalNumbers.Status.toUpperCase()){
    case "HARAM":
        halalColor = Color.Danger
        break;
    case "HALAL":
        halalColor = Color.Safe
        break;
    default:
        halalColor = Color.Medium
        break;        
  }
}

if(allNumbers){
   switch(allNumbers.Safety.level.toUpperCase()){
     case "DANGEROUS":
       safeColor = Color.Danger
       break;
     case "SAFE":
       safeColor = Color.Safe
       break;
     default:
       safeColor = Color.Medium
       break;   
   }
}


    return(
        <>
      <div className={toggle ? "extension" : "extend"} onClick={() => handleClose()}></div> 
      <div className={toggle ? "extension-box" : "extend"} style={{ background: Color.Secondary}}>
        <div className={toggle ? "extension-main" : "extend"}>   
          <h1 style={{color: Color.Primary}}>{item}</h1>
          <h3 style={{color: Color.FontMain}} id="name">{allNumbers ? allNumbers.Name : "Not found"}</h3>
            <div style={{width: "80%", border: `2px solid ${Color.Primary}`}}></div> 
            <div className="extension-body">
              <h2 style={{color: Color.FontMain}}>Description</h2>
              <div className="extension-child" >
                <div id='line'></div>
                <p> {allNumbers? allNumbers.Description || "Not available" : "Not available"} </p>
              </div> 
              <h2 style={{color: Color.FontMain}}>Catagory</h2> 
              <div className="extension-child" >
                <div id='line'></div>
                <p>{halalNumbers ? halalNumbers.Catagory || "Not available" : "Not available"}</p>
              </div>
              <h2 style={{color: Color.FontMain}}>Usage</h2> 
              <div className="extension-child" >
                <div id='line'></div>
                <p>{allNumbers? allNumbers.Use || "Not available" : "Not available"}</p>
              </div>
              <h2 style={{color: Color.FontMain}}>Daily limit</h2>
              <div className="extension-child" >
                <div id='line'></div>
                <p>{allNumbers? allNumbers.Safety.Daily || "Not available" : "Not available"}</p>
              </div> 
              <h2 style={{color: Color.FontMain}}>Halal Status</h2> <div id="horizontal-line"> <h4 style={{color: halalColor}}>{halalNumbers? halalNumbers.Status || "Not available" : "Not available"}</h4></div>
              <div className="extension-child" >
                <div id='line'></div>
                <p>{halalNumbers? halalNumbers.Description || "Not available" : "Not available"}</p>
              </div> 
              <h2 style={{color: Color.FontMain}}>Safety level</h2> <div id="horizontal-line"> <h4 style={{color: safeColor}}>{allNumbers? allNumbers.Safety.level || "Not available" : "Not available"} </h4></div>
              <div className="extension-child" >
                <div id='line'></div>
                <p>{allNumbers? allNumbers.Safety.description || "Not available" : "Not available"}</p>
              </div> 
              <h2 style={{color: Color.FontMain}}>Vegan </h2> <div id="horizontal-line"> {nonVeganNumbers ? <h4 style={{color: Color.Danger}}>Vegans can Avoid</h4> : <h4 style={{color: Color.Safe}}> {veganNumbers ? veganNumbers.status || "Not available" : <div style={{color: Color.Medium}}> No data </div> } </h4> }</div>
              <div className="extension-child" >
                <div id='line'></div>
                <p>{nonVeganNumbers? nonVeganNumbers.Details || "Not available" : "could be suitable"}</p>
              </div> 
     <div className="btn-group" >
       <button style={{background: Color.Primary, color: Color.Secondary}} onClick={()=> handleClose()}>Close</button>
       <button style={{border: `2px solid ${Color.Primary}`, color: Color.Primary}}  onClick={()=> handleNextClick()} >Next</button>
    </div>
         </div>
         <div style={{width: "80%", border: `2px solid ${Color.Primary}`}}></div> 
       </div>
       <div className="some-space"></div>
  </div>
        </>  
    )
}

export default Extension;