
import Product from '../../Assests/products.png'
import { useColorScheme } from "../../Store/Store";

const Body = () => {

const Color = useColorScheme(state => state.data)

    return(
        <div className="body" style={{background: Color.Secondary}}>
             <div className="circle-one" style={{background: Color.Primary}}></div>
             <div className="circle-two" style={{background: Color.Primary}}></div>
             <div className="circle-three" style={{background: Color.Primary}}></div>
             <h1 className="title"> <span>E</span>-NUMBERS </h1>
             <img src={Product} className="product-image" alt="enumber"  style={{background: `linear-gradient(50deg, ${Color.Primary} 55%, ${Color.Primary} 100%)`}} />

             <div className="body-text" >
                 <ul>
                     <li>E100s are generally colours.</li>
                     <li>E200 to E282 are mainly preservatives and acids.</li>
                     <li>E300 to E341 are mainly antioxidants and acid regulators.</li>
                     <li>E400s include emulsifiers, stabilisers, thickeners, anti-caking agents, release agents and bulking agents.</li>  <br/>
                     <li style={{color: Color.Primary, listStyle: "none", fontWeight: 700}} >   To know more about an E number, search it using search box. </li> 
                 </ul>
             </div>

             <div className="fact-text">
                 <p> <span style={{color: Color.Primary, fontWeight: 700}}>Fact</span>: Many of these additives were once of natural origin. However, most are now prepared/produced synthetically as these are often less expensive than the natural product.</p>
             </div>
        </div>
    )
}

export default Body;