import axios from "axios";

export const getAllNumbers = (setData) => {
        axios
        .get("https://raw.githubusercontent.com/Shubhdeeps/personal/main/Enumbers/allNumbers.json")
        .then(response => {
            const data = response.data
           setData(data)
        })
}

export const getVeganNumbers = (setData) => {
    axios
    .get("https://raw.githubusercontent.com/Shubhdeeps/personal/main/Enumbers/veganNumbers.json")
    .then(response => {
        const data = response.data
        setData(data)
    })
}

